import 'babel-polyfill';
import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import Page from '../../common/page.js';
import InputNumber from 'yo3/component/inputnumber/';
import Dialog from 'yo3/component/dialog/';
import Picker from 'yo3/component/picker/';
import Popup from 'yo3/component/popup/';
import Touchable from 'yo3/component/touchable';


class Demo extends Component {
    constructor() {
        super();
        this.state = {
            show: false,
            title: 'title',
            content: 'content',
            okText: '',
            cancelText: '',
            position: 'center',
            animation: 'fade',
            contentOffsetY: 0,
            pickerShow1: false,
            pickerShow2: false
        };
    }

    /**
     * @property change
     * @param e {Object} 
     * @param key {String}
     * @description onChange, dialogstate
     */
    change(e, key) {
        this.state[key] = e.target.value;
        this.setState(this.state);
    }

    render() {
        const { show, title, content, okText, cancelText, position,
            animation, contentOffsetY, pickerShow1, pickerShow2 } = this.state;
        return (
            <Page title="Dialog Demo" scrollable onLeftPress={() => {
                location.href = './index.html';
            }}>
                <ul className="yo-list yo-list-panel">
                    <li className="item item-input">
                        <div className="mark">title</div>
                        <input className="input flex" value={title} onChange={e => this.change(e, 'title')}/>
                    </li>
                    <li className="item item-input">
                        <div className="mark">content</div>
                        <input
                            className="input flex" value={content}
                            onChange={e => this.change(e, 'content')}
                        />
                    </li>
                    <li className="item item-input">
                        <div className="mark">okText</div>
                        <input
                            className="input flex" value={okText}
                            onChange={e => this.change(e, 'okText')}
                        />
                    </li>
                    <li className="item item-input">
                        <div className="mark">cancelText</div>
                        <input
                            className="input flex" value={cancelText}
                            onChange={e => this.change(e, 'cancelText')}
                        />
                    </li>
                    <li className="item">
                        <div className="mark flex">Y(contentOffsetY)</div>
                        <InputNumber
                            min={-10}
                            value={contentOffsetY}
                            max={100}
                            step={10}
                            onChange={val => this.setState({ contentOffsetY: val })}
                        />
                    </li>
                    <li className="item" onTouchTap={() => this.setState({ pickerShow1: true })}>
                        <div className="flex">dialog</div>
                        <div>{position}</div>
                        <i className="yo-ico more">&#xf07f;</i>
                    </li>
                    <li className="item" onTouchTap={() => this.setState({ pickerShow2: true })}>
                        <div className="flex">dialog</div>
                        <div>{animation}</div>
                        <i className="yo-ico more">&#xf07f;</i>
                    </li>
                </ul>
                <Popup
                    show={pickerShow1}
                    height={150}
                    onMaskTap={() => this.setState({ pickerShow1: false })}
                >
                    <Picker
                        options={[{ value: 'top', text: 'top' }, { value: 'center', text: 'center' }, {
                            value: 'bottom',
                            text: 'bottom'
                        }]}
                        value={position}
                        looped={false}
                        onChange={val => this.setState({ position: val.value })}
                    />
                </Popup>
                <Popup
                    show={pickerShow2}
                    height={150}
                    onMaskTap={() => this.setState({ pickerShow2: false })}
                >
                    <Picker
                        options={[
                            { value: '' },
                            { value: 'fade' },
                            { value: 'fade-in-up' },
                            { value: 'fade-in-down' },
                            { value: 'zoom' }
                        ]}
                        value={this.state.animation}
                        looped={false}
                        onChange={val => this.setState({ animation: val.value })}
                    />
                </Popup>
                <Touchable onTap={() => this.setState({ show: true })} touchClass={'touchable-opacity'}>
                    <button className="yo-btn open-modal">
                        
                    </button>
                </Touchable>
                <Dialog
                    show={show}
                    animation={animation}
                    title={title}
                    contentOffset={[0, contentOffsetY]}
                    align={position}
                    okText={okText}
                    cancelText={cancelText}
                    onOk={() => this.setState({ show: false })}
                    onCancel={() => this.setState({ show: false })}
                >
                    {content}
                </Dialog>
            </Page>
        );
    }
}

ReactDOM.render(<Demo />, document.querySelector('#content'));


